import component from "@/components/ksp-image-cutter/ksp-image-cutter.vue"
export default {
	components: {
		component
	},
	data() {
		return {
			src: ""
		}
	},
	

	methods: {
		takePhoto() {
			const ctx = uni.createCameraContext();
			ctx.takePhoto({
				quality: 'high',
				success: (res) => {
					this.src = res.tempImagePath
				}
			});
		},
		error(e) {
			console.log(555);
		},
		delPhoto() {
			this.src = ""
		}
	}
}
